import {
    Avatar,
    Container, Divider,
    Grid, List,
    ListItem,
    ListItemAvatar,
    ListItemText,
    Paper,
} from "@mui/material";
import Web3 from "web3";
import coineus_abi from '../abis/coineus.json';
import fuseone_abi from '../abis/fuseone.json';

import {useSelector} from "react-redux";
import {useEffect, useState} from "react";
import {makeBatchRequest} from "../utils/promisify";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import {formatWeiToNumber} from "../utils/format";
import consensus_abi from "../abis/consensus_abi.json";
import {makeStyles} from "@mui/styles";
import clsx from "clsx";
import dividend_distributor_abi from "../abis/DividendDistributor.json";

const coineus_fund_address = "0xb80267eA7fa368374ee4d4Bf10044778232AdEFe";
const assurance_fund_address = "0x09501F8Ca9476d0c516E121FB4dCE4B7b2301d51";
const coineus_validator_address = "0x53e2033F86C1a54bEbAE893DC8C9fAe179b3Faa2";
const consensusAddress = "0x3014ca10b91cb3D0AD85fEf7A3Cb95BCAc9c0f79";
const FUSEONE_CONTRACT_ADDRESS = "0x10C59F4F9AdF164f4aff4Db9fa59AE1e1764977e";

const useStyles = makeStyles((theme) => ({
    networkChip: {
        borderRadius: "50%",
        marginLeft: 30,
        position: 'absolute',
        top: 42,
        height: 16,
        width: 16,
        display: 'inline-block',
        backgroundSize: 'cover',
        '&.fuse': {
            backgroundImage: `url('https://coineus.app/assets/tokens/fuse/logo.png')`,
        },
        '&.avax': {
            backgroundImage: `url('https://coineus.app/assets/tokens/avax/logo.png')`,
        },
        '&.bnb': {
            backgroundImage: `url('https://coineus.app/assets/tokens/bnb/logo.png')`,
        },
        '&.eth': {
            backgroundImage: `url('https://coineus.app/assets/tokens/eth/logo.png')`,
        }
    }
}));

export default function CoineusPortfolio() {

    const {wallet, rpc} = useSelector(state => state.coineus);

    const [data, setData] = useState({});

    const [fuseData, setFuseData] = useState({});

    useEffect(() => {

        const web3FUSE = new Web3(rpc.fuse);
        const consensusContract = new web3FUSE.eth.Contract(consensus_abi, consensusAddress);
        const volt_contract = new web3FUSE.eth.Contract(coineus_abi, '0x34Ef2Cc892a88415e9f02b91BfA9c91fC0bE6bD4');
        const eth_contract = new web3FUSE.eth.Contract(coineus_abi, '0xa722c13135930332Eb3d749B2F0906559D2C5b99');
        const bnb_contract = new web3FUSE.eth.Contract(coineus_abi, '0x6acb34b1Df86E254b544189Ec32Cf737e2482058');
        const btc_contract = new web3FUSE.eth.Contract(coineus_abi, '0x33284f95ccb7B948d9D352e1439561CF83d8d00d');

        const cnsvolt_contract = new web3FUSE.eth.Contract(coineus_abi, '0x836c205C49D9Db143f0A79DD384d08895f171C5f');
        const cnseth_contract = new web3FUSE.eth.Contract(coineus_abi, '0x5c1bB0b286B5Ba67b776f596aCA6625BAf9CCDd5');
        const cnsbnb_contract = new web3FUSE.eth.Contract(coineus_abi, '0x5351B7eB69D81731f8C7A5948C1c0570fBd82f48');
        const cnsbtc_contract = new web3FUSE.eth.Contract(coineus_abi, '0xF78116881138A2793E5820997cabF7268b6099ea');
        const cnsfuse_contract = new web3FUSE.eth.Contract(coineus_abi, '0xeFA4222Fd80328558801Fe78ba3392048432d891');


        const fuseone_contract = new web3FUSE.eth.Contract(fuseone_abi, '0x301158EB212fD5626C41611cbCce8Da4aF81B9ff');

        const callsF = [
            web3FUSE.eth.getBalance(coineus_fund_address),
            web3FUSE.eth.getBalance(assurance_fund_address),
            consensusContract.methods.delegatedAmount(coineus_fund_address, coineus_validator_address).call,
            consensusContract.methods.delegatedAmount(assurance_fund_address, coineus_validator_address).call,
            consensusContract.methods.delegatedAmount(FUSEONE_CONTRACT_ADDRESS, coineus_validator_address).call,

            volt_contract.methods.balanceOf(coineus_fund_address).call,
            eth_contract.methods.balanceOf(coineus_fund_address).call,
            bnb_contract.methods.balanceOf(coineus_fund_address).call,
            btc_contract.methods.balanceOf(coineus_fund_address).call,

            cnsvolt_contract.methods.balanceOf(coineus_fund_address).call,
            cnsvolt_contract.methods.getAccountDividendsInfo(coineus_fund_address).call,
            cnseth_contract.methods.balanceOf(coineus_fund_address).call,
            cnseth_contract.methods.getAccountDividendsInfo(coineus_fund_address).call,
            cnsbnb_contract.methods.balanceOf(coineus_fund_address).call,
            cnsbnb_contract.methods.getAccountDividendsInfo(coineus_fund_address).call,
            cnsbtc_contract.methods.balanceOf(coineus_fund_address).call,
            cnsbtc_contract.methods.getAccountDividendsInfo(coineus_fund_address).call,
            cnsfuse_contract.methods.balanceOf(coineus_fund_address).call,
            cnsfuse_contract.methods.getAccountDividendsInfo(coineus_fund_address).call,

            fuseone_contract.methods.balanceOf(coineus_fund_address).call,
            fuseone_contract.methods.balanceOf(assurance_fund_address).call,
        ];

        makeBatchRequest(web3FUSE, callsF).then(resp => {
            let [
                coineus_fund_balance,
                assurance_fund_balance,
                coineus_fund_staked,
                assurance_fund_staked,
                fuseone_staked,
                volt_balance,
                eth_balance,
                bnb_balance,
                btc_balance,

                cnsvolt_balance,
                cnsvolt_info,
                cnseth_balance,
                cnseth_info,
                cnsbnb_balance,
                cnsbnb_info,
                cnsbtc_balance,
                cnsbtc_info,
                cnsfuse_balance,
                cnsfuse_info,

                coineus_fund_fuseone,
                assurance_fund_fuseone,

            ] = resp;

            coineus_fund_balance = coineus_fund_balance / (10 ** 18);
            assurance_fund_balance = assurance_fund_balance / (10 ** 18);
            coineus_fund_staked = coineus_fund_staked / (10 ** 18);
            assurance_fund_staked = assurance_fund_staked / (10 ** 18);
            fuseone_staked = fuseone_staked / (10 ** 18);

            volt_balance = volt_balance / (10 ** 18);
            eth_balance = eth_balance / (10 ** 18);
            bnb_balance = bnb_balance / (10 ** 18);
            btc_balance = btc_balance / (10 ** 8);

            cnsvolt_balance = cnsvolt_balance / (10 ** 18);
            cnseth_balance = cnseth_balance / (10 ** 18);
            cnsbnb_balance = cnsbnb_balance / (10 ** 18);
            cnsbtc_balance = cnsbtc_balance / (10 ** 18);
            cnsfuse_balance = cnsfuse_balance / (10 ** 18);

            coineus_fund_fuseone = coineus_fund_fuseone / (10 ** 18);
            assurance_fund_fuseone = assurance_fund_fuseone / (10 ** 18);

            setFuseData(
                {
                    coineus_fund_balance,
                    assurance_fund_balance,
                    coineus_fund_staked,
                    assurance_fund_staked,
                    fuseone_staked,
                    volt_balance,
                    eth_balance,
                    bnb_balance,
                    btc_balance,
                    cnsvolt_balance,
                    cnsvolt_earnings: cnsvolt_info[4] / (10 ** 18),
                    cnseth_balance,
                    cnseth_earnings: cnseth_info[4] / (10 ** 18),
                    cnsbnb_balance,
                    cnsbnb_earnings: cnsbnb_info[4] / (10 ** 18),
                    cnsbtc_balance,
                    cnsbtc_earnings: cnsbtc_info[4] / (10 ** 8),
                    cnsfuse_balance,
                    cnsfuse_earnings: cnsfuse_info[4] / (10 ** 18),

                    coineus_fund_fuseone,
                    assurance_fund_fuseone
                }
            )
        })


        const web3AVAX = new Web3(rpc.avax);
        //rewards

        //holdings
        const usdc_contract = new web3AVAX.eth.Contract(coineus_abi, '0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E');

        const burnit_contract = new web3AVAX.eth.Contract(coineus_abi, '0x9CcAd19669259C1FBF13FDa959400C871b3d0E00');
        const burnit_dividends_contract = new web3AVAX.eth.Contract(dividend_distributor_abi, '0x32b82215a5ebb48acf7faadded562bc175716920');

        const dgnx_contract = new web3AVAX.eth.Contract(coineus_abi, '0x51e48670098173025C477D9AA3f0efF7BF9f7812');
        const babykrakens_contract = new web3AVAX.eth.Contract(coineus_abi, '0xA2Df0Dc89dfc39FAEe6cf13DFBf2203fEA3670E4');


        const calls = [
            web3AVAX.eth.getBalance(coineus_fund_address),

            usdc_contract.methods.balanceOf(coineus_fund_address).call,

            burnit_contract.methods.balanceOf(coineus_fund_address).call,
            burnit_dividends_contract.methods.shares(coineus_fund_address).call,

            dgnx_contract.methods.balanceOf(coineus_fund_address).call,

            babykrakens_contract.methods.balanceOf(coineus_fund_address).call,
            babykrakens_contract.methods.getAccountDividendsInfo(coineus_fund_address).call,
        ];

        makeBatchRequest(web3AVAX, calls).then(resp => {
            let [
                coineus_avax_balance,

                coineus_fund_usdc_balance,

                coineus_fund_burnit_balance,
                coineus_fund_burnit_shares,

                coineus_fund_dgnx_balance,

                coineus_fund_babykrakens_balance,
                coineus_fund_babykrakens_dividends

            ] = resp;

            setData({
                coineus_avax_balance: coineus_avax_balance / (10 ** 18),

                coineus_fund_usdc_balance: coineus_fund_usdc_balance / (10 ** 6),

                coineus_fund_burnit_balance,
                coineus_fund_burnit_dividends: coineus_fund_burnit_shares[2] / (10 ** 6),

                coineus_fund_dgnx_balance,

                coineus_fund_babykrakens_balance,
                coineus_fund_babykrakens_dividends: coineus_fund_babykrakens_dividends[4] / (10 ** 6),
            })

        });

    }, []);

    const totalValue =
        ((data.coineus_fund_burnit_balance / (10 ** 9)) * wallet.prices.avax?.['0x9CcAd19669259C1FBF13FDa959400C871b3d0E00']?.inUSD) + //burnit
        (
            (
                fuseData.coineus_fund_balance +
                fuseData.coineus_fund_staked +
                fuseData.assurance_fund_balance +
                fuseData.assurance_fund_staked +
                fuseData.coineus_fund_fuseone +
                fuseData.assurance_fund_fuseone
            ) * wallet.prices.fuse?.native?.inUSD
        ) + //FUSE
        data.coineus_fund_usdc_balance + //USDC
        (data.coineus_avax_balance * wallet.prices.avax.native?.inUSD) + //AVAX
        formatWeiToNumber(data.coineus_fund_dgnx_balance) * wallet.prices.avax?.['0x51e48670098173025C477D9AA3f0efF7BF9f7812']?.inUSD + //DGNX
        formatWeiToNumber(data.coineus_fund_babykrakens_balance) * wallet.prices.avax?.['0xA2Df0Dc89dfc39FAEe6cf13DFBf2203fEA3670E4']?.inUSD +//BABYKRAKENS
        fuseData.volt_balance * wallet.prices.fuse?.['0x34Ef2Cc892a88415e9f02b91BfA9c91fC0bE6bD4']?.inUSD +//VOLT
        fuseData.eth_balance * wallet.prices.fuse?.['0xa722c13135930332Eb3d749B2F0906559D2C5b99']?.inUSD +//ETH on FUSE
        fuseData.bnb_balance * wallet.prices.fuse?.['0x6acb34b1Df86E254b544189Ec32Cf737e2482058']?.inUSD +//BNB on FUSE
        fuseData.btc_balance * wallet.prices.fuse?.['0x33284f95ccb7B948d9D352e1439561CF83d8d00d']?.inUSD +//BTC on FUSE
        fuseData.cnsvolt_balance * wallet.prices.fuse?.['0x836c205C49D9Db143f0A79DD384d08895f171C5f']?.inUSD +//CNSvolt
        fuseData.cnseth_balance * wallet.prices.fuse?.['0x5c1bB0b286B5Ba67b776f596aCA6625BAf9CCDd5']?.inUSD + //CNSeth
        fuseData.cnsbnb_balance * wallet.prices.fuse?.['0x5351B7eB69D81731f8C7A5948C1c0570fBd82f48']?.inUSD + //CNSbnb
        fuseData.cnsbtc_balance * wallet.prices.fuse?.['0xF78116881138A2793E5820997cabF7268b6099ea']?.inUSD +//CNSbtc
        fuseData.cnsfuse_balance * wallet.prices.fuse?.['0xeFA4222Fd80328558801Fe78ba3392048432d891']?.inUSD //CNSbtc

    return <Container maxWidth="sm">
        <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>
            <Grid item xs={12}>
                <Paper style={{color: '#000', backgroundColor: '#fbca00'}}>
                    <ListItem>
                        <ListItemText
                            primary={CoineusUSDFormat(totalValue)}
                            secondary="Total Fund Value"
                            primaryTypographyProps={{fontWeight: 700, fontSize: '2.125rem', textAlign: 'right'}}
                            secondaryTypographyProps={{color: '#000', textAlign: 'right'}}
                        />
                    </ListItem>
                </Paper>
            </Grid>

            <Grid item xs={12}>
                <Divider>FUSE</Divider>
            </Grid>

            <TokenStatsSection data={{
                symbol: "FUSE",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/fuse/logo.png",
                usdValue: CoineusUSDFormat((
                    fuseData.coineus_fund_balance +
                    fuseData.coineus_fund_staked +
                    fuseData.assurance_fund_balance +
                    fuseData.assurance_fund_staked +
                    fuseData.coineus_fund_fuseone +
                    fuseData.assurance_fund_fuseone
                ) * wallet.prices.fuse?.native?.inUSD),
                balance: CoineusCryptoFormat(fuseData.coineus_fund_balance + fuseData.assurance_fund_balance),
                secondaryBalance: CoineusCryptoFormat(fuseData.coineus_fund_staked + fuseData.assurance_fund_staked + fuseData.coineus_fund_fuseone + fuseData.assurance_fund_fuseone),
                secondarySymbol: 'FUSE',
                secondaryLabel: 'Staked',
                network: 'fuse',
                native: true
            }}/>

            <TokenStatsSection data={{
                symbol: "CNSfuse",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/fuse/0xeFA4222Fd80328558801Fe78ba3392048432d891/logo.png",
                usdValue: CoineusUSDFormat(fuseData.cnsfuse_balance * wallet.prices.fuse?.['0xeFA4222Fd80328558801Fe78ba3392048432d891']?.inUSD),
                balance: CoineusCryptoFormat(fuseData.cnsfuse_balance),
                secondaryBalance: CoineusCryptoFormat(fuseData.cnsfuse_earnings),
                secondarySymbol: 'WFUSE',
                secondaryLabel: 'Earned All-time',
                network: 'fuse'
            }}/>

            <TokenStatsSection data={{
                symbol: "VOLT",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/fuse/0x34Ef2Cc892a88415e9f02b91BfA9c91fC0bE6bD4/logo.png",
                usdValue: CoineusUSDFormat(fuseData.volt_balance * wallet.prices.fuse?.['0x34Ef2Cc892a88415e9f02b91BfA9c91fC0bE6bD4']?.inUSD),
                balance: CoineusCryptoFormat(fuseData.volt_balance),
                network: 'fuse'
            }}/>

            <TokenStatsSection data={{
                symbol: "CNSvolt",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/fuse/0x836c205C49D9Db143f0A79DD384d08895f171C5f/logo.png",
                usdValue: CoineusUSDFormat(fuseData.cnsvolt_balance * wallet.prices.fuse?.['0x836c205C49D9Db143f0A79DD384d08895f171C5f']?.inUSD),
                balance: CoineusCryptoFormat(fuseData.cnsvolt_balance),
                secondaryBalance: CoineusCryptoFormat(fuseData.cnsvolt_earnings),
                secondarySymbol: 'VOLT',
                secondaryLabel: 'Earned All-time',
                network: 'fuse'
            }}/>

            <TokenStatsSection data={{
                symbol: "WBTC",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/fuse/0x33284f95ccb7B948d9D352e1439561CF83d8d00d/logo.png",
                usdValue: CoineusUSDFormat(fuseData.btc_balance * wallet.prices.fuse?.['0x33284f95ccb7B948d9D352e1439561CF83d8d00d']?.inUSD),
                balance: CoineusCryptoFormat(fuseData.btc_balance),
                network: 'fuse'
            }}/>

            <TokenStatsSection data={{
                symbol: "CNSbtc",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/fuse/0xF78116881138A2793E5820997cabF7268b6099ea/logo.png",
                usdValue: CoineusUSDFormat(fuseData.cnsbtc_balance * wallet.prices.fuse?.['0xF78116881138A2793E5820997cabF7268b6099ea']?.inUSD),
                balance: CoineusCryptoFormat(fuseData.cnsbtc_balance),
                secondaryBalance: CoineusCryptoFormat(fuseData.cnsbtc_earnings),
                secondarySymbol: 'WBTC',
                secondaryLabel: 'Earned All-time',
                network: 'fuse'
            }}/>

            <TokenStatsSection data={{
                symbol: "WETH",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/fuse/0xa722c13135930332Eb3d749B2F0906559D2C5b99/logo.png",
                usdValue: CoineusUSDFormat(fuseData.eth_balance * wallet.prices.fuse?.['0xa722c13135930332Eb3d749B2F0906559D2C5b99']?.inUSD),
                balance: CoineusCryptoFormat(fuseData.eth_balance),
                network: 'fuse'
            }}/>

            <TokenStatsSection data={{
                symbol: "CNSeth",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/fuse/0x5c1bB0b286B5Ba67b776f596aCA6625BAf9CCDd5/logo.png",
                usdValue: CoineusUSDFormat(fuseData.cnseth_balance * wallet.prices.fuse?.['0x5c1bB0b286B5Ba67b776f596aCA6625BAf9CCDd5']?.inUSD),
                balance: CoineusCryptoFormat(fuseData.cnseth_balance),
                secondaryBalance: CoineusCryptoFormat(fuseData.cnseth_earnings),
                secondarySymbol: 'WETH',
                secondaryLabel: 'Earned All-time',
                network: 'fuse'
            }}/>

            <TokenStatsSection data={{
                symbol: "WBNB",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/fuse/0x6acb34b1Df86E254b544189Ec32Cf737e2482058/logo.png",
                usdValue: CoineusUSDFormat(fuseData.bnb_balance * wallet.prices.fuse?.['0x6acb34b1Df86E254b544189Ec32Cf737e2482058']?.inUSD),
                balance: CoineusCryptoFormat(fuseData.bnb_balance),
                network: 'fuse'
            }}/>

            <TokenStatsSection data={{
                symbol: "CNSbnb",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/fuse/0x5351B7eB69D81731f8C7A5948C1c0570fBd82f48/logo.png",
                usdValue: CoineusUSDFormat(fuseData.cnsbnb_balance * wallet.prices.fuse?.['0x5351B7eB69D81731f8C7A5948C1c0570fBd82f48']?.inUSD),
                balance: CoineusCryptoFormat(fuseData.cnsbnb_balance),
                secondaryBalance: CoineusCryptoFormat(fuseData.cnsbnb_earnings),
                secondarySymbol: 'WBNB',
                secondaryLabel: 'Earned All-time',
                network: 'fuse'
            }}/>

            <Grid item xs={12}>
                <Divider>AVAX</Divider>
            </Grid>

            <TokenStatsSection data={{
                symbol: "AVAX",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/avax/logo.png",
                usdValue: CoineusUSDFormat(data.coineus_avax_balance * wallet.prices.avax.native?.inUSD),
                balance: CoineusCryptoFormat(data.coineus_avax_balance),
                network: 'avax',
                native: true
            }}/>

            <TokenStatsSection data={{
                symbol: "USDC",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/avax/0xB97EF9Ef8734C71904D8002F8b6Bc66Dd9c48a6E/logo.png",
                usdValue: CoineusUSDFormat(data.coineus_fund_usdc_balance),
                network: 'avax'
            }}/>

            <TokenStatsSection data={{
                symbol: "BBK",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/avax/0xA2Df0Dc89dfc39FAEe6cf13DFBf2203fEA3670E4/logo.png",
                usdValue: CoineusUSDFormat((data.coineus_fund_babykrakens_balance / (10 ** 18)) * wallet.prices.avax?.['0xA2Df0Dc89dfc39FAEe6cf13DFBf2203fEA3670E4']?.inUSD),
                balance: CoineusCryptoFormat((data.coineus_fund_babykrakens_balance / (10 ** 18))),
                secondaryBalance: CoineusCryptoFormat((data.coineus_fund_babykrakens_dividends)),
                secondarySymbol: 'USDC',
                secondaryLabel: 'Earned All-time',
                network: 'avax'
            }}/>

            <TokenStatsSection data={{
                symbol: "BRN",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/avax/0x9CcAd19669259C1FBF13FDa959400C871b3d0E00/logo.png",
                usdValue: CoineusUSDFormat((data.coineus_fund_burnit_balance / (10 ** 9)) * wallet.prices.avax?.['0x9CcAd19669259C1FBF13FDa959400C871b3d0E00']?.inUSD),
                balance: CoineusCryptoFormat((data.coineus_fund_burnit_balance / (10 ** 9))),
                secondaryBalance: CoineusCryptoFormat((data.coineus_fund_burnit_dividends)),
                secondarySymbol: 'USDC',
                secondaryLabel: 'Earned All-time',
                network: 'avax'
            }}/>

            <TokenStatsSection data={{
                symbol: "DGNX",
                label: "Fund Holdings",
                logo: "https://coineus.app/assets/tokens/avax/0x51e48670098173025C477D9AA3f0efF7BF9f7812/logo.png",
                usdValue: CoineusUSDFormat((data.coineus_fund_dgnx_balance / 10 ** 18) * wallet.prices.avax?.['0x51e48670098173025C477D9AA3f0efF7BF9f7812']?.inUSD),
                balance: CoineusCryptoFormat((data.coineus_fund_dgnx_balance / 10 ** 18)),
                network: 'avax'
            }}/>

        </Grid>
    </Container>
}

function TokenStatsSection(props) {

    const classes = useStyles();

    const {
        native,
        symbol,
        label,
        logo,
        usdValue,
        balance,
        secondaryBalance,
        secondarySymbol,
        secondaryLabel,
        network
    } = props.data;

    return <Grid item xs={12}>
        <Paper>
            <List>
                <ListItem>
                    <ListItemAvatar>
                        <Avatar src={logo}/>
                        {!native && <span className={clsx(classes.networkChip, network)}/>}
                    </ListItemAvatar>
                    <ListItemText
                        primary={symbol}
                        primaryTypographyProps={{fontWeight: 700}}
                        secondary={label}
                    />
                    <ListItemText
                        primary={usdValue}
                        secondary="USD Value"
                        primaryTypographyProps={{fontWeight: 700, textAlign: 'right'}}
                        secondaryTypographyProps={{textAlign: 'right'}}
                    />
                </ListItem>
                {
                    balance !== undefined && <ListItem>
                        <ListItemText
                            primary={`${balance} ${symbol}`}
                            primaryTypographyProps={{fontWeight: 700}}
                            secondary="Wallet"
                        />
                        {
                            secondaryBalance !== undefined && <ListItemText
                                primary={`${secondaryBalance} ${secondarySymbol}`}
                                primaryTypographyProps={{fontWeight: 700}}
                                secondary={secondaryLabel}
                            />
                        }
                    </ListItem>
                }
            </List>
        </Paper>
    </Grid>
}