import Web3 from "web3";
import fuseone_abi from '../abis/fuseone.json';
import getUserBalances from "../utils/get_balances";

import {makeBatchRequest} from "../utils/promisify";
import {useEffect, useState} from "react";
import {useSelector} from "react-redux";
import {
    Alert,
    Box,
    Button,
    Container,
    FormControl,
    Grid, InputAdornment, LinearProgress,
    List,
    ListItem,
    ListItemText,
    Paper, Slide, Snackbar, Tab, Tabs,
    TextField,
    Typography
} from "@mui/material";

import {toWei} from '../utils/format';
import CoineusLoading from "../components/CoineusLoading";
import {CoineusCryptoFormat, CoineusUSDFormat} from "../utils/currency_format";
import consensus_abi from "../abis/consensus_abi.json";

const web3 = new Web3('https://rpc.fuse.io');

const FUSEONE_CONTRACT_ADDRESS = '0x301158EB212fD5626C41611cbCce8Da4aF81B9ff';
const FUSEONE_CONTRACT = new web3.eth.Contract(fuseone_abi, FUSEONE_CONTRACT_ADDRESS);

const coineusValidatorAddress = "0x53e2033F86C1a54bEbAE893DC8C9fAe179b3Faa2";
const consensusAddress = "0x3014ca10b91cb3D0AD85fEf7A3Cb95BCAc9c0f79";
const consensusContract = new web3.eth.Contract(consensus_abi, consensusAddress);

export default function FuseOne() {

    const {wallet} = useSelector(state => state.coineus);

    function TransitionUp(props) {
        return <Slide {...props} direction="up"/>;
    }

    const [snackbarOpen, setSnackbarOpen] = useState(false);
    const [snackbarMessage, setSnackbarMessage] = useState("Testing Snackbar");
    const [snackbarSeverity, setSnackbarSeverity] = useState("success");
    const [snackbarTransition, setSnackbarTransition] = useState(undefined);

    const [pending, setPending] = useState(false);
    const [amount, setAmount] = useState("");
    const [data, setData] = useState({});
    const [connectedContract, setConnectedContract] = useState(0);
    const [action, setAction] = useState('stake');
    const [displayInUSD, setDisplayInUSD] = useState(false);

    const getFuseone = () => {
        if (!wallet.address) {
            return;
        }

        const web3b = new Web3(window.provider);
        setConnectedContract(new web3b.eth.Contract(fuseone_abi, FUSEONE_CONTRACT_ADDRESS));

        const calls = [
            FUSEONE_CONTRACT.methods.totalSupply().call,
            FUSEONE_CONTRACT.methods.balanceOf(wallet.address).call,
            FUSEONE_CONTRACT.methods.contractDelegatedAmount().call,
            consensusContract.methods.stakeAmount(coineusValidatorAddress).call,
        ];

        makeBatchRequest(web3, calls).then(resp => {

            const [
                supply,
                balance,
                delegated_amount,
                total_staked
            ] = resp;

            setData({
                supply,
                balance,
                delegated_amount : delegated_amount / (10**18),
                total_staked: total_staked / (10**18)
            })

        })
        getUserBalances();
    }


    useEffect(() => {
        getFuseone();
        const interval = setInterval(getFuseone, 10000)
        return () => clearInterval(interval);
    }, [wallet.address, wallet.chain]);

    const delegate = async () => {
        try {
            const tx = connectedContract.methods.deposit();
            const gas = await tx.estimateGas({
                from: wallet.address,
                value: toWei(amount)
            })
            const gasPrice = 10000000000;//await web3.eth.getGasPrice();
            const data = tx.encodeABI();
            const txData = {
                from: wallet.address,
                value: toWei(amount),
                to: FUSEONE_CONTRACT_ADDRESS,
                data: data,
                gas,
                gasPrice,
            }

            tx.send(txData)
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getFuseone();
                    setPending(false);
                    setAmount('');
                })
                .catch(() => {
                });
        } catch (e) {
            handleError({message: e.toString()})
        }

    }

    const withdraw = async () => {
        try {
            const tx = connectedContract.methods.withdraw(toWei(amount))
            const gas = await tx.estimateGas({
                from: wallet.address
            })
            const gasPrice = 10000000000;//await web3.eth.getGasPrice();
            const data = tx.encodeABI();
            const txData = {
                from: wallet.address,
                to: FUSEONE_CONTRACT_ADDRESS,
                data: data,
                gas,
                gasPrice,
            }

            tx.send(txData)
                .once('transactionHash', function (tx) {
                    setPending(true)
                })
                .on('error', function (error) {
                    setPending(false)
                })
                .then(function (receipt) {
                    getFuseone();
                    setPending(false);
                    setAmount('');
                })
                .catch(() => {
                });
        } catch (e) {
            handleError({message: e.toString()})
        }
    }

    const handleError = (error) => {
        if (error.message) {
            setSnackbarTransition(() => TransitionUp)
            setSnackbarSeverity("error");
            setSnackbarMessage(error.message);
            setSnackbarOpen(true);
        }
    }


    return (
        <Container maxWidth="sm">
            <Grid container spacing={2} style={{marginTop: -5, paddingBottom: 20}}>

                <Grid item xs={12}>
                    <Paper style={{padding: 20}} onClick={() => setDisplayInUSD(!displayInUSD)}>
                        <Typography
                            variant="h4">{displayInUSD ? CoineusUSDFormat(data.delegated_amount * wallet.prices.fuse?.native?.inUSD) : CoineusCryptoFormat(data.delegated_amount)} </Typography>

                        <Typography variant="body2">FUSEONE Staked Amount</Typography>
                        <LinearProgress variant="determinate" value={(data.delegated_amount / data.total_staked) * 100}
                                        style={{marginTop: 10}}/>
                        <Typography variant="caption" textAlign="right"
                                    style={{display: 'block'}}>{CoineusCryptoFormat(data.delegated_amount)} /
                            {CoineusCryptoFormat(data.total_staked)}</Typography>
                    </Paper>
                </Grid>

                {
                    wallet.chain === 122 ? <Grid item xs={12}>
                        <Paper>
                            <Tabs
                                variant="fullWidth"
                                value={action}
                                onChange={(ev, val) => {
                                    setAction(val)
                                    setAmount('')
                                }}
                            >
                                <Tab id="stake" value="stake" label="STAKE"/>
                                <Tab id="unstake" value="unstake" label="UNSTAKE"/>
                            </Tabs>
                            {
                                action === "stake" && <Box style={{padding: 20}}>
                                    <List>
                                        <ListItem disableGutters disablePadding>
                                            <ListItemText
                                                secondary="FUSE Balance"
                                            />
                                            <ListItemText
                                                secondary={`${CoineusCryptoFormat(wallet.balances.fuse.native)} FUSE`}
                                                secondaryTypographyProps={{textAlign: 'right'}}
                                            />
                                        </ListItem>
                                    </List>
                                    <FormControl fullWidth style={{marginBottom: 20}}>
                                        <TextField
                                            autoComplete="off"
                                            type="number"
                                            value={amount}
                                            placeholder="Enter Amount"
                                            onChange={(ev) => {
                                                if (ev.target.value > 5000000) return;
                                                setAmount(ev.target.value)
                                            }}
                                            InputProps={{
                                                endAdornment: <InputAdornment position="end">
                                                    <Button variant="contained" onClick={() => {
                                                        setAmount(wallet.balances.fuse.native - .1)
                                                    }
                                                    }>MAX</Button>
                                                </InputAdornment>,
                                            }}
                                        />
                                    </FormControl>
                                    <Button fullWidth variant="contained" disabled={!amount || amount > 5000000}
                                            onClick={delegate}>Stake</Button>
                                </Box>
                            }

                            {
                                action === "unstake" && <Box style={{padding: 20}}>
                                    <List>
                                        <ListItem disableGutters disablePadding>
                                            <ListItemText
                                                secondary="FUSEONE Balance"
                                            />
                                            <ListItemText
                                                secondary={`${CoineusCryptoFormat(data.balance / (10 ** 18))} FUSEONE`}
                                                secondaryTypographyProps={{textAlign: 'right'}}
                                            />
                                        </ListItem>
                                    </List>
                                    <FormControl fullWidth style={{marginBottom: 20}}>
                                        <TextField
                                            autoComplete="off"
                                            type="number"
                                            value={amount}
                                            placeholder="Enter Amount"
                                            onChange={(ev) => {
                                                if (ev.target.value > data.balance / (10 ** 18)) return;
                                                setAmount(ev.target.value)
                                            }}
                                            InputProps={{
                                                endAdornment: <InputAdornment position="end">
                                                    <Button variant="contained" onClick={() => {
                                                        setAmount(data.balance / (10 ** 18))
                                                    }
                                                    }>MAX</Button>
                                                </InputAdornment>,
                                            }}
                                        />
                                    </FormControl>
                                    <Button fullWidth variant="contained"
                                            disabled={!amount || amount > data.balance / (10 ** 18)}
                                            onClick={withdraw}>Unstake</Button>
                                </Box>
                            }

                        </Paper>
                    </Grid>
                        :
                        <Grid item xs={12}>
                            <Paper style={{padding:20}}>
                                <Typography>Connect to Fuse Network</Typography>
                            </Paper>
                        </Grid>
                }

            </Grid>

            <CoineusLoading open={pending} label="Transaction Pending"/>

            <Snackbar
                open={snackbarOpen}
                anchorOrigin={{vertical: "bottom", horizontal: "center"}}
                autoHideDuration={10000}
                onClose={() => {
                    setSnackbarOpen(false);
                }}
                TransitionComponent={snackbarTransition}
                sx={{bottom: {xs: 30}}}
            >
                <Alert
                    color={snackbarSeverity}
                    variant="filled"
                    elevation={6}
                    icon={false}
                >
                    <Typography>{snackbarMessage}</Typography>
                </Alert>
            </Snackbar>

        </Container>
    )
}